import { Component, OnInit } from '@angular/core';
import { PessoaService } from '../pessoa.service';
import { pessoaModel } from './pessoa-model';

@Component({
  selector: 'app-pessoas-listagem',
  templateUrl: './pessoas-listagem.component.html',
  styleUrls: ['./pessoas-listagem.component.css']
})
export class PessoasListagemComponent implements OnInit {

  pessoa: pessoaModel = new pessoaModel();
  pessoas: Array<any>;

  constructor(private pessoaService: PessoaService) { }

  ngOnInit() {
    this.listar();
  }

  cadastrar() {
    console.log(this.pessoa);
    this.pessoaService.cadastrarPessoa(this.pessoa).subscribe(pessoa => {
      this.pessoa = new pessoaModel();
      this.listar();

    }, err => {
      console.log('Erro ao cadastrar o aluno', err);

    })
  }

  listar() {
    this.pessoaService.listar().subscribe(dados => this.pessoas = dados);
  }

  remover(id: number) {
    this.pessoaService.removerPessoa(id).subscribe(() => {
      this.pessoa = new pessoaModel();
      this.listar();

    }, err => {
      console.log('Erro ao cadastrar o aluno', err);

    })
  }

}
